// main.js
import { createApp } from 'vue';
import App from './App.vue';

// Vuetify 
import { createVuetify } from 'vuetify';
import 'vuetify/styles'; // Vuetify 
import '@mdi/font/css/materialdesignicons.css'; // MDI 
import * as components from 'vuetify/components'; // Vuetify 
import * as directives from 'vuetify/directives'; // Vuetify 

const vuetify = createVuetify({
    components,
    directives,
});

const app = createApp(App);
app.use(vuetify);
app.mount('#app');